@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;500;700&display=swap');

:root {
  --pri: #15172d;
  --sec: #cf4147;
  --ter: #eae2b6;
}

*{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Fira Sans', sans-serif;
  color: var(--ter);
  background-color: var(--pri);
}

h1 {
  margin: 0;
  font-size: 3rem;
  text-transform: uppercase;
}

h2 {
  margin: 0;
  padding: 2px 7px;
  font-size: 1.7rem;
  background-color: var(--sec);
  border-radius: 3px;
  width: max-content;
  position: absolute;
  bottom: 5px;
  left: 5px;
}

h3 {
  margin: 0;
  font-size: 1rem;
  font-weight: 500;
  line-height: 22px;
}

p {
  margin: 0;
  font-size: 1.3rem;
  line-height: 27px;
}

img {
  width: 100%;
  border-radius: 3px;
}

hr {
  width: 50%;
  border: 1px solid var(--sec);
}

.right {
  margin: 0 0 0 auto;
}

.left {
  margin: 0 auto 0 0;
}

.container {
  max-width: 1111px;
  margin: 0 auto;
  padding: 0 15px;
}

.box {
  display: grid;
  padding: 50px 0;
}

.title {
  color: var(--sec);
  margin-bottom: 10px;
}

.home {
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.home span {
  color: var(--sec);
}

.about, .contact {
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  gap: 50px;
}

.work .list{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  margin-top: 7px;
}

.work .image {
  position: relative;
  height: 250px;
}

.work .image .icon {
  color: var(--sec);
  position: absolute;
  font-size: 1.7rem;
  top: 5px;
  right: 5px;
  cursor: pointer;
}

.work img {
  height: 100%;
  background-color: var(--ter);
}

.contact span {
  color: var(--sec);
}

.contact .icon {
  font-size: 3rem;
  margin: 17px 10px 0 0;
  color: var(--ter);
  cursor: pointer;
}

.contact form {
  display: grid;
}

.contact label {
  font-size: 1.3rem;
  margin-bottom: 5px;
}

.contact input, .contact textarea, .contact button {
  outline: none;
  padding: 10px;
  color: var(--ter);
  border-radius: 3px;
}

.contact input, .contact textarea {
  border: 2px solid var(--sec);
  background-color: transparent;
  font-size: 1rem;
  margin-bottom: 15px;
  resize: none;
  font-family: 'Fira Sans', sans-serif;
  font-weight: 500;
}

.contact button {
  background-color: var(--sec);
  border: none;
  font-size: 1.3rem;
  margin-top: 10px;
}

.contact .dis {
  filter: brightness(75%);
}

.footer {
  padding: 20px 0 !important;
}

.alert {
  position: fixed;
  top: 5px;
  right: 5px;
  padding: 5px;
  background-color: var(--sec);
  border-radius: 3px;
}

@media screen and (max-width: 900px) {
  .work .list {
      grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 700px) {
  .about, .contact, .work .list {
      grid-template-columns: repeat(1, 1fr);
  }
}
